@font-face {
  font-family: "PT Sans";
  src: local("PT Sans"), local("PTSans-Regular"), url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
  font-style: normal;
  font-weight: 400;
  font-display: swap;
}
@font-face {
  font-family: "PT Sans";
  src: local("PT Sans Bold"), local("PTSans-Bold"), url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
  font-style: normal;
  font-weight: 700;
  font-display: swap;
}
body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-family: "PT Sans", "Arial", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.5;
  text-align: center;
  color: #000000;
  background-color: #ffffff;
}
html {
  box-sizing: border-box;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
a {
  text-decoration: none;
}
img {
  max-width: 100%;
  height: auto;
}
.container {
  max-width: 1200px;
  margin: 0 auto;
  width: 100%;
  box-shadow: 0 5px 15px 0 rgba(0, 1, 1, 0.2);
}
.visually-hidden {
  position: absolute;
  margin: -1px;
  padding: 0;
  width: 1px;
  height: 1px;
  border: 0;
  clip: rect(0, 0, 0, 0);
  overflow: hidden;
}
.button {
  display: inline-block;
  font-size: 21px;
  font-weight: bold;
  line-height: 1.24;
  color: #ffffff;
  text-align: center;
  vertical-align: middle;
  letter-spacing: normal;
  text-decoration: none;
  background-color: #82b4d3;
  border: none;
}
.button:hover,
.button:focus {
  background-color: #669fc1;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
  background-color: #5397be;
}
.button:disabled {
  background-color: #e5e5e5;
}
.advantages__review {
  padding: 42px 32px 34px;
}
.advantages__title {
  margin: 0;
  font-size: 18px;
  font-weight: bold;
  line-height: 1.44;
  color: #000000;
}
.advantages__subtitle {
  margin-top: 30px;
  line-height: 1.85;
  color: #333333;
}
.advantages__list {
  margin: 0;
  padding: 0;
  list-style: none;
}
.advantages__item {
  padding: 34px 27px 26px;
  position: relative;
  color: #ffffff;
  background-color: #82b4d3;
}
.advantages__item--inactive {
  padding: 26px 5px;
  margin-bottom: -14px;
  color: #000000;
  background-color: #efefef;
}
.advantages__item--active::before {
  content: "";
  position: absolute;
  top: -26px;
  left: 0;
  width: 320px;
  height: 26px;
  background-image: url("../img/bg-intro-triangle-mobile-top.svg");
  background-position: center;
  background-repeat: no-repeat;
}
.advantages__heading {
  margin: 0;
  font-size: 18px;
  font-weight: bold;
  line-height: 1.44;
}
.advantages__number {
  line-height: 1;
}
.advantages__description {
  margin-top: 20px;
}
.advantages__description--inactive {
  padding: 0 27px;
}
.advantages__description--mark::before {
  content: "";
  position: absolute;
  top: -1px;
  left: 20px;
  height: 1px;
  width: calc(100% - 40px);
  background-color: #d7d7d7;
}
.advantages__picture {
  width: 100%;
  height: 260px;
  background-color: brown;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
.advantages__picture--city {
  background-image: url("../img/main-city-mobile@1x.jpg");
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .advantages__picture--city {
    background-image: url("../img/main-city-mobile@2x.jpg");
  }
}
.advantages__picture--bridge {
  background-image: url("../img/main-bridge-mobile@1x.jpg");
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .advantages__picture--bridge {
    background-image: url("../img/main-bridge-mobile@2x.jpg");
  }
}
@media (min-width: 768px) {
  .advantages__review {
    padding: 47px 0 36px;
    max-width: 490px;
    margin: 0 auto;
  }
  .advantages__title {
    font-size: 21px;
  }
  .advantages__subtitle {
    margin-top: 24px;
  }
  .advantages__box {
    display: flex;
    flex: wrap;
  }
  .advantages__box--bridge {
    flex-direction: row-reverse;
  }
  .advantages__item {
    padding: 45px 30px 20px;
    width: calc(100% / 3);
  }
  .advantages__item--active::before {
    display: none;
  }
  .advantages__item--inactive {
    padding: 39px 22px 30px;
  }
  .advantages__heading {
    font-size: 21px;
    line-height: 1.24;
  }
  .advantages__heading--bridge {
    padding: 0 23px;
  }
  .advantages__heading--inactive {
    padding: 0 27px;
  }
  .advantages__description {
    margin-top: 24px;
    padding: 0 20px;
  }
  .advantages__description--mark::before {
    display: none;
    padding: 0 32px;
  }
  .advantages__description--road {
    padding: 0 32px;
  }
  .advantages__picture {
    width: calc(100% / 3 * 2);
  }
  .advantages__picture--city {
    background-image: url("../img/main-city-tablet@1x.jpg");
  }
  .advantages__picture--bridge {
    background-image: url("../img/main-bridge-tablet@1x.jpg");
  }
}
@media (min-width: 768px) and (min-resolution: 144dpi), (min-width: 768px) and (min-resolution: 1.5dppx) {
  .advantages__picture--city {
    background-image: url("../img/main-city-tablet@2x.jpg");
  }
}
@media (min-width: 768px) and (min-resolution: 144dpi), (min-width: 768px) and (min-resolution: 1.5dppx) {
  .advantages__picture--bridge {
    background-image: url("../img/main-bridge-tablet@2x.jpg");
  }
}
@media (min-width: 1200px) {
  .advantages__subtitle {
    line-height: 1.86;
  }
  .advantages__item {
    padding: 53px 51px 31px;
  }
  .advantages__item--bridge {
    padding: 53px 66px 41px;
  }
  .advantages__item--inactive {
    padding: 41px 51px 51px;
  }
  .advantages__heading {
    padding: 0 42px;
  }
  .advantages__heading--bridge {
    padding: 0 59px;
  }
  .advantages__heading--inactive {
    padding: 0 75px;
  }
  .advantages__description--inactive {
    padding: 0;
  }
  .advantages__description--mark {
    padding: 0 12px;
  }
  .advantages__picture--city {
    background-image: url("../img/main-city-desktop@1x.jpg");
  }
  .advantages__picture--bridge {
    background-image: url("../img/main-bridge-desktop@1x.jpg");
  }
}
@media (min-width: 1200px) and (min-resolution: 144dpi), (min-width: 1200px) and (min-resolution: 1.5dppx) {
  .advantages__picture--city {
    background-image: url("../img/main-city-desktop@2x.jpg");
  }
}
@media (min-width: 1200px) and (min-resolution: 144dpi), (min-width: 1200px) and (min-resolution: 1.5dppx) {
  .advantages__picture--bridge {
    background-image: url("../img/main-bridge-desktop@2x.jpg");
  }
}
.advice {
  padding: 35px 20px 20px 20px;
  margin: 0;
  list-style: none;
}
.advice__item {
  position: relative;
  padding-left: 50px;
  text-align: left;
}
.advice__item--housing::before {
  content: "";
  position: absolute;
  top: -8px;
  left: 0;
  width: 37px;
  height: 36px;
  background-image: url("../img/icon-house.svg");
}
.advice__item--souvenir::before {
  content: "";
  position: absolute;
  top: -8px;
  left: 4px;
  width: 32px;
  height: 38px;
  background-image: url("../img/icon-gift.svg");
}
.advice__item--food::before {
  content: "";
  position: absolute;
  top: 0;
  left: -4px;
  width: 40px;
  height: 35px;
  background-image: url("../img/icon-burger.svg");
}
.advice__title {
  margin: 0;
  font-size: 18px;
  font-weight: bold;
  line-height: 1.44;
  margin-bottom: 20px;
}
.advice__description {
  margin-bottom: 20px;
  line-height: 1.5;
  color: #333333;
}
.advice__transfer {
  display: none;
}
@media (min-width: 768px) {
  .advice {
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: 155px 0 43px;
  }
  .advice__title {
    font-size: 21px;
    line-height: 1.24;
  }
  .advice__item {
    position: relative;
    padding: 0;
    max-width: 163px;
    text-align: center;
  }
  .advice__item--housing::before {
    top: -99px;
    left: 43px;
    width: 75px;
    height: 72px;
    background-image: url("../img/icon-house-tablet.svg");
  }
  .advice__item--souvenir {
    order: 2;
  }
  .advice__item--souvenir::before {
    top: -102px;
    left: 48px;
    width: 64px;
    height: 75px;
    background-image: url("../img/icon-gift-tablet.svg");
  }
  .advice__item--food {
    padding: 0 5px;
  }
  .advice__item--food::before {
    top: -96px;
    left: 42px;
    width: 77px;
    height: 68px;
    background-image: url("../img/icon-burger-tablet.svg");
  }
}
@media (min-width: 1200px) {
  .advice {
    padding-bottom: 61px;
  }
  .advice__item {
    max-width: 300px;
  }
  .advice__item--housing::before {
    left: 112px;
  }
  .advice__item--souvenir::before {
    left: 116px;
  }
  .advice__item--food::before {
    left: 111px;
  }
  .advice__description--souvenir {
    padding: 0 53px;
  }
}
.main-nav {
  position: relative;
}
.main-nav__toggle {
  position: absolute;
  top: 16px;
  right: 20px;
  z-index: 2;
  width: 23px;
  height: 22px;
  outline: none;
  cursor: pointer;
  border: none;
}
.main-nav--closed .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 23px;
  height: 22px;
  background-image: url("../img/icon-menu.svg");
  background-repeat: no-repeat;
  background-color: #82b4d3;
  outline: none;
  border: none;
  cursor: pointer;
}
.main-nav--opened .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 23px;
  height: 22px;
  background-image: url("../img/icon-cross.svg");
  background-repeat: no-repeat;
  background-color: #82b4d3;
  outline: none;
  border: none;
  cursor: pointer;
}
@media (max-width: 767px) {
  .main-nav--closed .site-list__item {
    display: none;
  }
  .main-nav--closed .site-list__item--active {
    display: block;
  }
  .main-nav--nojs .main-nav__toggle {
    display: none;
  }
}
@media (min-width: 768px) {
  .main-nav {
    background-color: #ffffff;
  }
  .main-nav__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
  }
  .main-nav__list li:nth-child(2) {
    margin-right: auto;
  }
  .main-nav__list li:nth-child(3) {
    margin-left: auto;
  }
  .main-nav__toggle {
    display: none;
  }
}
@media (min-width: 1200px) {
  .main-nav__toggle {
    display: none;
  }
}
