.feedback-form__label--radio {
  position: relative;
  display: block;
  padding-top: 14px;
  padding-bottom: 29px;
  padding-left: 80px;
  font-size: 18px;
  font-weight: bold;
  line-height: 1.44;
}
.feedback-form__input-radio + label::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 56px;
  height: 56px;
  border: 5px solid #f2f2f2;
  background-color: #f2f2f2;
  border-radius: 50%;
}
.feedback-form__input-radio:checked + label::after {
  content: "";
  position: absolute;
  top: 17px;
  left: 17px;
  width: 21px;
  height: 21px;
  border: 3px solid #333333;
  background-color: #333333;
  border-radius: 50%;
