  border: 5px solid #f2f2f2;
  border-radius: 5px;
  background-color: #f2f2f2;
  cursor: pointer;
}
.feedback-form__input-checkbox:checked + label::after {
  content: "";
  position: absolute;
  top: 16px;
  left: 16px;
  width: 24px;
  height: 16px;
  background: url("../img/icon-tick.svg") no-repeat;
